<template>
  <div class="message_list">
    <!-- 面包屑 -->
    <breadcrumb :routeMeta="$route.meta"></breadcrumb>
    <!-- 卡片 -->
    <el-card>
      <el-button @click="handleReadClick">确定已读</el-button>
      <!-- 用户数据展示的表格 -->
      <el-table
        :data="messageList"
        border
        stripe
        @cell-click="handleMessageDetail"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="title" label="标题"></el-table-column>
        <el-table-column prop="content" label="内容">
          <template #default="val">
            <span class="table_content">{{val.row.content}}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态">
          <template #default="val">
            <el-tag v-if="val.row.read==true" type="success">已读</el-tag>
            <el-tag v-else type="info">未读</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="issue_time" label="发布时间"></el-table-column>
      </el-table>
      <!-- 分页 -->
      <pagination
        :total="listTotal"
        :pageSizes="[5, 10, 20]"
        :layout="'total,sizes,prev,pager,next'"
        :queryInfo="queryInfo"
        :queryFunc="getMessageList"
      ></pagination>
    </el-card>
    <!-- 主体页面 结束 -->

    <!-- 功能弹窗 开始 -->
    <!-- 添加用户的弹窗 开始-->
    <el-dialog :visible.sync="messageDialog" width="50%" @close="handleCloseDialog">
      <div class="msgbody">
        <div class="msg_wrap">
          <div class="title">{{messageInfo.title}}</div>
          <div class="content">{{messageInfo.content}}</div>
        </div>
        <div class="issue_time">{{messageInfo.issue_time}}</div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button v-if="messageInfo.read==true" type="primary" @click="messageDialog = false">关 闭</el-button>
          <el-button v-else type="primary" @click="handleSureReadEvnet">确定已读</el-button>
        </span>
      </template>
    </el-dialog>
    <!-- 添加用户的弹窗 结束 -->
  </div>
</template>

<script>
import Breadcrumb from "@/components/Breadcrumb.vue";
import Pagination from "@/components/Pagination.vue";
import { mapState } from "vuex";

import { getNotifyListApi } from "@/api/api.js";

export default {
  name: "message",
  created() {
    this.getMessageList();
  },
  mounted() {},
  computed: {
    ...mapState("menu", ["powerList"])
  },
  data() {
    return {
      listTotal: null,
      // 查询信息
      queryInfo: {
        pageParam: {
          page: 1,
          pageSize: 10
        }
      },
      // 弹窗的打开判断
      messageDialog: false,
      // 用户表单
      messageInfo: {
        title: "",
        content: "",
        issue_time: "",
        read: false
      },
      // 具体数据
      messageList: [],
      // 全选的id list
      idList: []
    };
  },
  methods: {
    async getMessageList() {
      let res = await getNotifyListApi({
        page: this.queryInfo.pageParam.page,
        pageSize: this.queryInfo.pageParam.pageSize
      });
      console.log(res);
      if (res.code != 10000) {
        return this.$message.error(res.message);
      }

      this.messageList = res.value;
      this.listTotal = res.num;
    },
    // 查看信息
    handleMessageDetail(row, column, cell) {
      if (column.label == "内容") {
        this.messageDialog = true;
        this.messageInfo = {
          ...row
        };
        console.log(row);
      }
    },
    // 选项框
    handleSelectionChange(val) {
      this.idList = [];
      if (val.length == 0) {
        return;
      }
      console.log(val);

      val.forEach(v => {
        if (!v.read) {
          this.idList.push(v.id);
        }
      });

      console.log(this.idList);
    },
    // 关闭 dialog 弹窗
    handleCloseDialog() {
      // 1. 清空表单信息
      this.message = {
        title: "",
        content: ""
      };
    },
    // 确定单条以读
    handleSureReadEvnet() {
      console.log(this.messageInfo);
      this.$emit("handleSureReadEvnet", this.messageInfo.id);
      this.messageDialog = false;
      // this.getMessageList();
    },
    // 确定所有已读
    async handleReadClick() {
      if (this.idList.length == 0) {
        return;
      }

      this.$emit("handleSureReadEvnet", this.idList);
      // let params = {
      //   notify_ids: this.idList
      // };
      // let res = await getNotifyListCheckApi(params);

      // if (res.code != 10000) {
      //   this.$message.error(res.message);
      // }

      // this.$message.success("全部已读～");

      // this.getMessageList();
    }
  },

  components: {
    Breadcrumb,
    Pagination
  }
};
</script>

<style lang="scss" scoped>
.msgbody {
  position: relative;
  .msg_wrap {
    height: 40vh;
    overflow: auto;
  }
}
.msg_wrap {
  .title {
    font-size: 20px;
    color: #f55;
  }
  .content {
    text-indent: 24px;
  }
}
.issue_time {
  position: absolute;
  bottom: -20px;
  right: 0;
}

.table_content {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
</style>