import Vue from "vue";
import Vuex from "vuex";
import tabsModules from "./tabs/tabsModules";
import menuModules from "./menu/menuModules";
import * as type from "./mutation-type";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    ownInfo: {},
  },
  mutations: {
    [type.EDIT_OWNINFO](state, payload) {
      state.ownInfo = { ...payload };
    },
    [type.CHANGE_OWNINFO](state, payload) {
      state.ownInfo[payload.key] = payload.value;
    },
  },
  actions: {},
  modules: {
    tabs: tabsModules,
    menu: menuModules,
  },
});
