<template>
  <div class="tabs">
    <el-tabs
      :value="activeTab"
      type="card"
      closable
      @tab-remove="handleRemoveTab"
      @tab-click="handleTabClick"
      @contextmenu.prevent.native="openContextMenu($event)"
    >
      <el-tab-pane
        :key="item.name"
        v-for="item in pageTabs"
        :label="item.title"
        :name="item.name"
      >{{ item.content }}</el-tab-pane>
    </el-tabs>

    <ul
      v-show="contextMenuVisible"
      class="contextmenu"
      :style="{top:menuTop+'px',left:menuLeft+'px'}"
    >
      <li class="menu_item" @click="handleRemoveThis">关闭标签</li>
      <li class="menu_item" @click="handleRemoveTotal">关闭所有</li>
      <li class="menu_item" @click="handleRemoveLeft">关闭左侧</li>
      <li class="menu_item" @click="handleRemoveRight">关闭右侧</li>
    </ul>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import * as type from "@/store/mutation-type.js";

export default {
  created() {
    // 默认进入第一个标签页
    this[type.EDIT_ACTIVE_TAB](this.pageTabs[0].name);
  },
  computed: {
    ...mapState("tabs", ["pageTabs", "activeTab"])
  },
  data() {
    return {
      contextMenuVisible: false,
      menuTop: 0,
      menuLeft: 0,

      tabPath: null
    };
  },
  watch: {
    contextMenuVisible(newVal) {
      // 监听 show 并关闭 document的 点击事件。
      if (newVal) {
        document.addEventListener("click", this.contextMenuFunc);
      } else {
        document.removeEventListener("click", this.contextMenuFunc);
      }
    }
  },
  methods: {
    ...mapMutations("tabs", [type.EDIT_PAGE_TABS, type.EDIT_ACTIVE_TAB]),
    // 关闭标签页
    handleRemoveTab(oldName) {
      let tabs = this.pageTabs;
      let activeName = this.activeTab;

      // 为了让被关闭的标签页不进行缓存
      this.$emit("excludeKeepAliveEvent", oldName);

      if (activeName === oldName) {
        tabs.forEach((tab, index) => {
          if (tab.name === oldName) {
            let nextTab = tabs[index + 1] || tabs[index - 1];
            if (nextTab) {
              activeName = nextTab.name;
            }
          }
        });
      }

      let tempAllTabs = tabs.filter(tab => tab.name !== oldName);

      // 如果 过滤之后发现最终长度为0，那么表示没标签，这时候要搞一个标签
      if (tempAllTabs.length == 0) {
        this[type.EDIT_PAGE_TABS]([{ title: "首页", name: "/home/welcome" }]);
        this[type.EDIT_ACTIVE_TAB]("/home/welcome");
        this.$router.push("/home/welcome");
        return;
      }

      this[type.EDIT_PAGE_TABS](tempAllTabs);
      this[type.EDIT_ACTIVE_TAB](activeName);
      this.$router.push(activeName);
    },

    //点击标签页
    handleTabClick(tab) {
      this[type.EDIT_ACTIVE_TAB](tab.name);

      // 根据 点击的tab，导航到该路径,并且更新 激活标签的值
      this.$router.push(tab.name);
    },

    // 右键菜单
    openContextMenu(e) {
      let path = e.target.id.split("-")[1];
      if (!path) {
        return;
      }

      // 将右键菜单的 path
      this.tabPath = path;

      this.contextMenuVisible = !this.contextMenuVisible;

      if (!this.contextMenuVisible) {
        return;
      }

      this.menuLeft = e.x;
      this.menuTop = e.y + 10;
    },

    // 关闭菜单
    contextMenuFunc() {
      this.contextMenuVisible = false;
    },

    /**
     *
     * 菜单的点击事件
     */
    handleRemoveThis() {
      this.handleRemoveTab(this.tabPath);
    },
    handleRemoveTotal() {
      this.pageTabs.forEach(v => {
        this.handleRemoveTab(v.name);
      });
    },
    handleRemoveLeft() {
      // this.tabPath
      // this.pageTabs
      let index = this.pageTabs.findIndex(v => v.name == this.tabPath);
      for (let i = 0; i < index; i++) {
        this.handleRemoveTab(this.pageTabs[0].name);
      }
    },
    handleRemoveRight() {
      let index = this.pageTabs.findIndex(v => v.name == this.tabPath);
      let maxIndex = this.pageTabs.length - 1;
      for (; index < maxIndex; ++index) {
        let nowMaxIndex = this.pageTabs.length - 1;
        this.handleRemoveTab(this.pageTabs[nowMaxIndex].name);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.tabs {
  user-select: none;
}

.contextmenu {
  margin: 0;
  padding: 0;
  list-style: none;

  position: absolute;
  cursor: pointer;
  z-index: 2;

  background-color: #eee;
  box-shadow: 0 0 4px #888;
  border-radius: 4px;

  .menu_item {
    font-size: 16px;
    color: #000;
    padding: 4px 10px;

    &:hover {
      background-color: #409eff;
    }
  }
}
</style>