// 验证
// 英文系统名
export const ruleName_en = (rule, value, callback) => {
  const reg = /^\w+$/;
  if (value === "") {
    return callback(new Error("请输入系统英文名"));
  }
  if (!reg.test(value)) {
    return callback(new Error("格式不正确!格式应该由英文，数字，下划线组成"));
  }
  callback();
};

// 账户名 /^[\u4e00-\u9fa5\w]{3,9}$/g
export const ruleAccount = (rule, value, callback) => {
  const reg = /^[\u4e00-\u9fa5\w]{3,9}$/g;
  if (value === "") {
    return callback(new Error("请输入账户名"));
  }
  if (!reg.test(value)) {
    return callback(
      new Error("账户名应该由3～9个汉字、英文，数字，下划线组成")
    );
  }
  callback();
};

// path地址
export const ruleAddr = (rule, value, callback) => {
  const reg = /\/[\w\/]+/;
  if (value === "") {
    return callback(new Error("请输入地址"));
  }
  if (!reg.test(value)) {
    return callback(new Error("格式不正确!格式应该类似 /xx "));
  }
  callback();
};

// 邮箱
export const ruleEmail = (rule, value, callback) => {
  const reg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
  if (value === "") {
    return callback(new Error("请输入正确的邮箱"));
  }
  if (!reg.test(value)) {
    return callback(new Error("格式不正确!"));
  }
  callback();
};

// 手机
export const ruleMobile = (rule, value, callback) => {
  const reg = /^1[3456789]\d{9}$/;
  if (value === "") {
    return callback(new Error("请输入正确的手机号"));
  }
  if (!reg.test(value)) {
    return callback(new Error("格式不正确!"));
  }
  callback();
};

export const rulePassword = (rule, value, callback) => {
  const reg = /^\w{6,18}$/;
  if (value === "") {
    return callback(new Error("请输入密码"));
  }
  if (!reg.test(value)) {
    return callback(new Error("格式不正确，密码长度在6～18个字符之间"));
  }
  callback();
};
