<template>
  <div class="pie_card" style="width:50%;height:350px"></div>
</template>

<script>
import echarts from "echarts";

export default {
  props: {
    chartData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      chart: null
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler(val) {
        this.setOptions(val);
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart();
    });
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el);
      this.setOptions(this.chartData);
    },
    setOptions({ pieData, pieName, pieColor } = {}) {
      this.chart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{b} <br/> {c} ({d}%)"
        },
        color: pieColor,
        legend: {
          left: "center",
          bottom: "10",
          data: pieName
        },
        series: [
          {
            type: "pie",
            // label: {
            //   normal: {
            //     show: true,
            //     formatter: "{b} : {c} 人  ({d}%)",
            //   },
            // },
            // center: ['50%', '38%'],
            radius: [10, 90],
            roseType: "area",
            data: pieData,
            animationEasing: "cubicInOut",
            animationDuration: 1000
          }
        ]
      });
    }
  },
  computed: {}
};
</script>

<style>
.pie_card {
  margin-top: 20px;
  display: inline-block;
  /* box-shadow: 0 0 6px #aaa; */
}
</style>
