export default {
  UserType(member) {
    switch (member) {
      case 0:
        return { name: "普通用户", color: "success" };
        break;
      case 1:
        return { name: "会员", color: "warning" };
        break;
      default:
        return { name: "没有", color: "info" };
        break;
    }
  },
  UserSex(sex) {
    switch (sex) {
      case 0:
        return { name: "女", color: "warning" };
        break;
      case 1:
        return { name: "男", color: "success" };
        break;
      default:
        return { name: "未知", color: "info" };
        break;
    }
  },

  MemberType(type) {
    switch (type) {
      case 1:
        return { name: "普通会员", color: "warning" };
        break;
      case 2:
        return { name: "青铜会员", color: "success" };
        break;
      case 3:
        return { name: "白金会员", color: "danger" };
        break;
      case 4:
        return { name: "钻石会员", color: "danger" };
        break;
      default:
        break;
    }
  },
};
