import * as type from "@/store/mutation-type";

const state = {
  pageTabs: [],
  activeTab: "",
};

const mutations = {
  // 增加一个pageTab
  [type.INCRE_PAGE_TABS](state, payload) {
    //  title: "首页", name: "/home/welcome"
    state.pageTabs.push(payload);
  },
  // 直接修改整个pageTab
  [type.EDIT_PAGE_TABS](state, payload) {
    state.pageTabs = payload;
  },
  // 修改一个 activeTab
  [type.EDIT_ACTIVE_TAB](state, payload) {
    state.activeTab = payload;
  },
};

export default {
  namespaced: true,
  state,
  mutations,
};
