import axios from "axios";
import router from "@/router/index.js";
import { Message } from "element-ui";

export function post(url, config) {
  let instance = axios.create({
    baseURL: "",
    timeout: 10000,
  });

  // 请求拦截
  instance.interceptors.request.use((config) => {
    config.headers.token = window.sessionStorage.getItem("token");
    // config.headers.sign_id = "CheShiId";
    // config.headers.sign_key = "CheShiKey";
    config.headers.sign_id = "QuanShengSystem";
    config.headers.sign_key = "QuanShengAK47";

    return config;
  });

  // 响应拦截
  instance.interceptors.response.use((res) => {
    console.log(res);
    if (res.data.code === -10001 || res.data.code === -10011) {
      Message.error(res.data.message);
      window.location.reload();
      return router.replace("/login");
    }

    return res;
  });
  return new Promise((resolve, reject) => {
    instance
      .post(url, config)
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err);
      });
  });
}

export function get(url, config) {
  let instance = axios.create({
    baseURL: "",
    timeout: 10000,
  });

  instance.interceptors.request.use((config) => {
    config.headers.Authorization = window.sessionStorage.getItem("token");

    console.log("进入拦截器");
    //拦截了都要返回
    return config;
  });

  return new Promise((resolve, reject) => {
    instance
      .get(url, config)
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err);
      });
  });
}
