<template>
  <div class="card_panel_item">
    <div class="card">
      <div class="card_left">
        <i
          class="iconfont"
          :class="[iconClass]"
          :style="{ '--iconColor': iconColor }"
          :data-iconid="id"
        ></i>
      </div>
      <div class="card_right">
        <div class="c_name">{{ cName }}</div>
        <div class="c_value">{{ trueValue }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: {
      type: Number,
      default: NaN
    },
    iconClass: {
      type: String,
      default: ""
    },
    iconColor: {
      type: String,
      default: "#40c9c6"
    },
    cName: {
      type: String,
      default: ""
    },
    cValue: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      trueValue: 0,
      upRange: 0
    };
  },
  created() {
    this.upRange = ~~(this.cValue * 0.02);
    this.numberUp(this.cValue);
  },
  methods: {
    numberUp(value) {
      requestAnimationFrame(() => {
        if (this.trueValue < value) {
          this.trueValue += this.upRange;

          if (this.trueValue >= value) {
            this.trueValue = value;
          }
          this.numberUp(value);
        }
      });
    }

    //     numberUp(value) {
    //   this.timer = setTimeout(()=>{
    //     if (this.trueValue < value) {
    //       this.trueValue++;
    //       this.numberUp(value);
    //     }else {
    //       clearTimeout(this.timer)
    //     }
    //   },1)

    // }
  }
};
</script>

