<template>
  <div class="line_column" style="width:100%;height:350px"></div>
</template>

<script>
import echarts from "echarts";

export default {
  props: {
    chartData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      chart: null
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler(val) {
        this.setOptions(val);
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart();
    });
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el);
      this.setOptions(this.chartData);
    },
    setOptions({ actualData } = {}) {
      this.chart.setOption({
        xAxis: {
          data: actualData.xAxis,
          boundaryGap: false,
          axisTick: {
            show: false
          }
        },
        grid: {
          left: 20,
          right: 20,
          bottom: 20,
          top: 20,
          containLabel: true
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          }
        },
        yAxis: {
          axisTick: {
            show: true
          }
        },
        legend: {
          data: [actualData.name]
        },
        series: [
          // {
          //   name: "bar",
          //   type: "bar",
          //   itemStyle: {
          //     normal: {
          //       color: "#f55",
          //     },
          //   },
          //   label: {
          //     show: true,
          //   },
          //   barWidth: 40,
          //   data: actualData,
          //   animationDuration: 2000,
          //   animationEasing: "cubicInOut",
          // },
          {
            name: actualData.name,
            smooth: true,
            type: "line",
            itemStyle: {
              normal: {
                color: "#3888fa",
                lineStyle: {
                  color: "#3888fa",
                  width: 2
                },
                areaStyle: {
                  color: "#f3f8ff"
                }
              }
            },
            data: actualData.data,
            animationDuration: 2000,
            animationEasing: "quadraticOut"
          }
        ]
      });
    }
  },
  computed: {}
};
</script>

<style>
.line_column {
  box-shadow: 0 0 6px #aaa;
}
</style>
