<template>
  <div class="bar_card" style="width:50%;height:350px"></div>
</template>

<script>
import echarts from "echarts";

export default {
  props: {
    chartData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      chart: null
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler(val) {
        this.setOptions(val);
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart();
    });
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el);
      this.setOptions(this.chartData);
    },
    setOptions({ actualData } = {}) {
      this.chart.setOption({
        xAxis: {
          data: actualData.xAxis,
          axisTick: {
            show: false
          }
        },
        grid: {
          left: 20,
          right: 20,
          bottom: 20,
          top: 20,
          containLabel: true
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          }
        },
        yAxis: {
          axisTick: {
            show: true
          }
        },
        legend: {
          data: [actualData.name]
        },
        series: [
          {
            name: actualData.name,
            type: "bar",
            itemStyle: {
              normal: {
                color: "#f55"
              }
            },
            label: {
              show: true
            },
            barWidth: 30,
            data: actualData.data,
            animationDuration: 1000,
            animationEasing: "cubicInOut"
          }
        ]
      });
    }
  },
  computed: {}
};
</script>

<style lang="scss" scoped>
.bar_card {
  margin-top: 20px;
  display: inline-block;
  // box-shadow: 0 0 6px #aaa;
}
</style>

