<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 头像位置 -->
      <div class="avatar_box">
        <img src="https://gitee.com/pengnima1/blogimage/raw/master/img/background/bg2.jpg" alt="头像" />
      </div>
      <!-- 表单位置 :model="loginForm" 是父传子给form，为了验证数据 -->
      <el-form ref="loginFromRef" :model="loginForm" :rules="loginRules" class="login_form">
        <!-- 账号 -->
        <el-form-item prop="login_account">
          <el-input v-model="loginForm.login_account" prefix-icon="iconfont iconicon_people_fill"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="iconfont iconlock" type="password"></el-input>
        </el-form-item>
        <!-- 登录，注册 -->
        <el-form-item class="btns">
          <el-button type="primary" @click="loginClick">登录</el-button>
          <el-button type="info" @click="registerClick">注册</el-button>
        </el-form-item>
      </el-form>
    </div>

    <el-dialog
      title="注册"
      :visible.sync="registerDialog"
      width="40%"
      @close="handleCloseDialog"
      :close-on-click-modal="false"
    >
      <el-form
        ref="registerFormRef"
        :model="userForm"
        status-icon
        :rules="formRules"
        label-width="120px"
      >
        <el-form-item label="账户名" prop="login_account">
          <el-input v-model="userForm.login_account" clearable></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="userForm.password" clearable></el-input>
        </el-form-item>
        <el-form-item label="再次输入密码" prop="repassword">
          <el-input type="password" v-model="userForm.repassword" clearable></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="userForm.name" clearable></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile_phone">
          <el-input type="text" v-model="userForm.mobile_phone" clearable></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input type="text" v-model="userForm.email" clearable></el-input>
        </el-form-item>
        <el-form-item label="邀请码">
          <el-input
            type="text"
            v-model="userForm.agency_code"
            placeholder="选填，若有代理人邀请，可填写其邀请码"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-select v-model="userForm.sex">
            <el-option label="男" :value="1"></el-option>
            <el-option label="女" :value="0"></el-option>
            <el-option label="未知" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="验证码" prop="code">
          <el-input type="text" v-model="userForm.code">
            <template #append>
              <el-button type="primary" >获取验证码</el-button>
            </template>
          </el-input>
        </el-form-item>-->
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="registerDialog = false">取 消</el-button>
        <el-button type="primary" @click="handleSureRegister">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { loginApi, getNotifyDialogList, signApi } from "@/api/api.js";
import {
  ruleEmail,
  ruleMobile,
  ruleAccount,
  rulePassword
} from "@/rules/index.js";
export default {
  created() {},
  data() {
    var ruleRepassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.userForm.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      registerDialog: false,
      loginForm: {
        login_account: "",
        password: "",
        createText: ""
      },
      loginRules: {
        login_account: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      },

      /**
      {
        "login_account": "string",
        "name": "string",
        "password": "string",
        "agency_code": "string",
        "mobile_phone": "string",
        "code": "string",
        "email": "string",
        "sex": 0
      }
       */
      userForm: {
        login_account: "",
        name: "",
        password: "",
        repassword: "",
        mobile_phone: "",
        email: "",
        agency_code: "",
        code: "", // 手机验证码
        sex: null
      },
      formRules: {
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max: 9, message: "长度在 2 到 9 个字符", trigger: "blur" }
        ],
        login_account: [
          { required: true, validator: ruleAccount, trigger: "blur" }
        ],
        password: [
          { required: true, validator: rulePassword, trigger: "blur" }
        ],
        repassword: [
          { required: true, validator: ruleRepassword, trigger: "blur" }
        ],
        email: [{ required: true, validator: ruleEmail, trigger: "blur" }],
        mobile_phone: [
          { required: true, validator: ruleMobile, trigger: "blur" }
        ],
        code: [{ required: true, message: "请输入验证码", trigger: "blur" }]
      }
    };
  },
  methods: {
    registerClick() {
      console.log("注册");
      this.userForm.sex = 2;
      this.registerDialog = true;
    },
    loginClick() {
      //登录前先对表单进行 预验证，通过则回调返回 true
      this.$refs.loginFromRef.validate(async valid => {
        if (!valid) return;

        let res = await loginApi(this.loginForm);

        if (res.code != 10000) {
          this.$message.error(res.message);
          return this.$router.replace("/login");
        }

        let menu = res.value.menuList;

        // 按钮列表
        let btnList = res.value.btnList;
        let list = {};
        btnList.forEach(v => {
          if (!list[v.parent_id]) {
            list[v.parent_id] = [];
          }
          list[v.parent_id].push(v);
        });
        console.log(list);

        this.$message.success(res.message);
        // 添加进 sessionStorage 里
        window.sessionStorage.setItem("menuList", JSON.stringify(menu));
        window.sessionStorage.setItem("btnList", JSON.stringify(list));

        window.sessionStorage.setItem("token", res.value.token);

        let obj = {
          login_account: this.loginForm.login_account,
          user_id: res.value.user_id,
          user_type: res.value.user_type,
          agency: res.value.agency,
          member: res.value.member
        };

        window.sessionStorage.setItem("ownInfo", JSON.stringify(obj));

        this.$router.push("/");
      });
    },
    // 关闭弹窗
    handleCloseDialog() {
      // 返回成初始值
      this.$refs.registerFormRef.resetFields();
    },
    // 注册接口
    handleSureRegister() {
      console.log("确定注册");

      // 有接口了，这里补充
      this.$refs.registerFormRef.validate(res => {
        if (!res) {
          return;
        }

        (async () => {
          let res = await signApi(this.userForm);

          if (res.code != 10000) {
            return this.$message.error(res.message);
          }

          this.$message.success(res.message);

          // 注册成功之后直接把 账号密码输入到 loginForm 中
          this.loginForm = {
            login_account: this.userForm.login_account,
            createText: ""
          };

          this.registerDialog = false;
        })();
      });
    }
  }
};
</script>

<style scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.avatar_box {
  width: 150px;
  height: 150px;
  position: absolute;

  left: 50%;
  transform: translate(-50%, -50%);

  border-radius: 50%;

  border: 1px solid #fff;
  padding: 10px; /* padding和背景颜色加起来 也可以用来做类似边框的感觉 */
  background-color: #fff;
  box-shadow: 0 0 10px #ddd;
}
.avatar_box img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #eee;
}
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: flex-end;
}
</style>
