<template>
  <el-container class="container_wrap">
    <el-header>
      <home-head
        :unread="unread"
        :user_name="user_name"
        @handleMessageList="handleMessageList"
        @handleUpgrade="handleUpgrade"
        @editInfo="getUserInfo(true)"
        @editPwd="getUserInfo(false)"
        @logoutClick="logoutClick"
      />
    </el-header>
    <el-container class="container_main">
      <el-aside width="200px">
        <!-- 侧边栏菜单开始~ -->
        <el-col>
          <!-- 根菜单设置 -->
          <el-menu
            background-color="#333744"
            text-color="#fff"
            :unique-opened="true"
            :default-active="routeActive"
            :collapse="isCollapse"
            :collapse-transition="false"
            router
          >
            <!-- 设置下拉菜单，具名slot是菜单的名字 -->
            <div class="munu" v-for="item in menuList" :key="item.id">
              <!-- 有孩子则表示为多级菜单 -->
              <div class="multi_menu" v-if="item.children">
                <el-submenu :index="item.id + ''">
                  <template #title>
                    <i :class="item.icon"></i>
                    <span>{{ item.name }}</span>
                  </template>
                  <!-- menu开启了路由，那么会以 menu-item的index作为跳转路径 -->
                  <el-menu-item v-for="item2 in item.children" :key="item2.id" :index="item2.addr">
                    <i class="el-icon-menu"></i>
                    {{ item2.name }}
                  </el-menu-item>
                </el-submenu>
              </div>
              <!-- 无孩子表示为单级菜单 -->
              <div class="single_menu" v-else>
                <el-menu-item :index="item.addr">
                  <i class="el-icon-menu"></i>
                  {{ item.name }}
                </el-menu-item>
              </div>
            </div>
          </el-menu>
        </el-col>
      </el-aside>
      <el-main class="main_router">
        <!-- tabs如果包裹着 router-view 会导致渲染2次 -->
        <tabs ref="tabsRef" @excludeKeepAliveEvent="excludeKeepAliveEvent"></tabs>
        <keep-alive :exclude="excludeName">
          <router-view
            @removeOperateTabEvent="removeOperateTabEvent"
            @handleSureReadEvnet="handleSureRead"
            ref="routerRef"
          ></router-view>
        </keep-alive>
        <!-- 主体路由跳转 -->
      </el-main>
    </el-container>

    <!-- 修改用户信息 -->
    <el-dialog title="修改用户信息" width="30%" :visible.sync="userDiaLog.editInfo">
      <!-- 对话框body主体 -->
      <el-form
        ref="editUserRef"
        status-icon
        :model="userForm"
        label-width="80px"
        :rules="loginRules"
      >
        <div v-if="editUser == true">
          <el-form-item label="账户名">
            <el-input v-model="userForm.login_account" disabled></el-input>
          </el-form-item>
          <el-form-item label="姓名" prop="name">
            <el-input v-model="userForm.name"></el-input>
          </el-form-item>
          <el-form-item label="手机号" prop="mobile_phone">
            <el-input type="text" v-model="userForm.mobile_phone"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input type="text" v-model="userForm.email"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-select v-model="userForm.sex">
              <el-option label="男" :value="1"></el-option>
              <el-option label="女" :value="0"></el-option>
              <el-option label="未知" :value="2"></el-option>
            </el-select>
          </el-form-item>
        </div>
        <div v-else>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="userForm.password"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <!-- 对话框footer -->
      <template #footer class="dialog-footer">
        <el-button @click="userDiaLog.editInfo = false">取 消</el-button>
        <el-button type="primary" @click="handleSureEditUser">确 定</el-button>
      </template>
    </el-dialog>

    <!-- 信息列表的弹窗  userDiaLog.msgInfo -->
    <el-dialog
      v-if="Object.keys(messageInfo).length != 0"
      class="msg_dialog"
      title="消息弹窗"
      :visible.sync="userDiaLog.msgInfo"
      @close="handleMessageClose"
    >
      <div class="msgbody">
        <!-- 列表展示 -->
        <div class="msg_wrap">
          <div class="title">{{ messageInfo.title }}</div>
          <div class="content">{{ messageInfo.content }}</div>
        </div>
        <div class="issue_time">{{ messageInfo.issue_time }}</div>
      </div>
      <template #footer>
        <el-button type="primary" @click="handleSureRead()">确定已读</el-button>
      </template>
    </el-dialog>
  </el-container>
</template>

<script>
import Tabs from "@/components/Tabs.vue";
import Pagination from "@/components/Pagination.vue";

import HomeHead from "./components/HomeHead";

import { mapState, mapMutations } from "vuex";
import * as type from "@/store/mutation-type.js";
import {
  getSysUserInfoApi,
  getSysUserUpdateApi,
  getSysUserUpdatePwdeApi,
  getNotifyDialogListApi,
  getNotifyCheckApi,
  getNotifyListCheckApi
} from "@/api/api";

export default {
  beforeDestroy() {
    this[type.EDIT_PAGE_TABS]([]);
    this[type.EDIT_ACTIVE_TAB]("");
    this.excludeName = [];
    console.log("destory");
  },
  created() {
    // 菜单 =================================
    let menuList = this.oneMenuList;
    // 制作侧边栏菜单
    this.menuList = [...this.menuList, ...menuList];

    console.log(this.ownInfo);

    this.user_name = this.ownInfo.login_account;

    // 非法路径 ===============================
    // 在不同页面刷新，要打开对应的标签页
    this.openPathTab();

    // 弹窗列表的显示
    this.getMessageInfo();
  },
  data() {
    return {
      /**
       *  menuList
       *      底层菜单为： id，name，addr
       *      上层菜单为： id，name，icon，children
       */
      messageInfo: {},
      userDiaLog: {
        editInfo: false,
        msgInfo: false
      },
      unread: 0,
      user_name: "",
      msgQueryInfo: {
        pageParam: {
          pageSize: 1,
          page: 1
        }
      },
      loginRules: {
        login_account: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 9, message: "长度在 3 到 9 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 6,
            max: 18,
            message: "长度在 6 到 18 个字符",
            trigger: "blur"
          }
        ]
      },
      editUser: true,
      // 用户表单
      userForm: {
        name: "",
        password: "",
        mobile_phone: "",
        email: "",
        sex: null,
        login_account: "",
        id: ""
      },
      menuList: [
        {
          id: "0-1",
          name: "首页",
          addr: "/home/welcome"
        }
      ],
      isCollapse: false,
      activeURL: "",
      // 做标签的
      tabs: [],
      activeTab: "",
      excludeName: [],

      editUser: true
    };
  },
  computed: {
    ...mapState(["ownInfo"]),
    ...mapState("tabs", ["pageTabs"]),
    ...mapState("menu", ["oneMenuList"]),
    routeActive() {
      // let path = this.$route.path.match(/^(\/[^\/]+)/);
      let path = this.$route.path;
      // path
      if (path == null) {
        return;
      }

      return path;
    }
  },
  methods: {
    ...mapMutations("tabs", [
      type.INCRE_PAGE_TABS,
      type.EDIT_PAGE_TABS,
      type.EDIT_ACTIVE_TAB
    ]),
    /**
     *
     *
     * 请求API方法
     */
    // 获取弹窗列表
    async getMessageInfo() {
      let res = await getNotifyDialogListApi();

      if (res.code != 10000) {
        this.$message.error(res.message);
        return this.$router.push("/login");
      }

      // console.log("res", res);

      if (res.num != 0) {
        // window.sessionStorage.setItem("notifyInfo", JSON.stringify(res.value));
        this.messageInfo = res.value || {};
        this.unread = res.num || 0;
      }

      if (Object.keys(this.messageInfo).length != 0) {
        this.userDiaLog.msgInfo = true;
      }
    },
    // 获取 本 用户信息
    async getUserInfo(editStatus) {
      this.userDiaLog.editInfo = true;
      this.editUser = editStatus;
      if (editStatus) {
        // 传
        console.log(this.ownInfo);

        let params = {
          id: this.ownInfo.user_id,
          login_account: this.ownInfo.login_account
        };
        let res = await getSysUserInfoApi(params);
        if (res.code == 10000) {
          this.userForm = { ...res.value };
        }
      }
    },
    // 确定修改用户
    async handleSureEditUser() {
      if (this.editUser) {
        let res = await getSysUserUpdateApi(this.userForm);
        if (res.code == 10000) {
          this.userDiaLog.editInfo = false;
        }
      } else {
        let params = {
          login_account: this.ownInfo.login_account,
          password: this.userForm.password
        };
        let res = await getSysUserUpdatePwdeApi(params);
        if (res.code == 10000) {
          this.$message.success(res.message);
          this.$router.replace("/login");
        }
      }
    },

    /**
     *
     *
     * 点击事件
     */
    // 打开对应的标签页
    openPathTab() {
      let route = this.$route;
    },
    // 点击铃铛
    handleMessageList() {
      console.log("点击了铃铛");
      this.$router.push("/message");
    },
    // 退出登陆
    logoutClick() {
      this.$router.push("/login");
      // 由于 vuex 还存在着，进入路由会默认从vuex中读取数据，所以每次退出最好 刷新一次，弄掉vuex
      location.reload();
      console.log("退出");
    },
    // 升级会员
    handleUpgrade() {
      console.log("点击了升级会员");
      this.$router.push("/upgrade");
    },
    // 关闭弹窗
    handleMessageClose() {
      // 清除 storage 里的key
      // window.sessionStorage.removeItem("notifyInfo");
    },
    // 确定以读
    async handleSureRead(id = null) {
      this.userDiaLog.msgInfo != false && (this.userDiaLog.msgInfo = false);
      // 发送已读的请求接口
      let res = null;
      let len = 0;
      if (id instanceof Array) {
        let params = {
          notify_ids: id
        };
        res = await getNotifyListCheckApi(params);
        len = id.length;
      } else {
        let params = {
          notify_id: id || this.messageInfo.id
        };
        res = await getNotifyCheckApi(params);
        len = 1;
      }

      if (res.code != 10000) {
        return this.$message.error(res.message);
      }

      this.unread -= len;
      this.$message.success("已读");
      if (this.$route.path == "/message") {
        this.$refs.routerRef.getMessageList();
      }
    },

    /**
     *
     *
     * emit 事件
     */
    // 去除keepalive缓存
    removeOperateTabEvent(oldPath) {
      this.excludeKeepAliveEvent(oldPath);
    },
    // 移除标签页(去除缓存)
    excludeKeepAliveEvent(oldName) {
      // 加正则进行匹配
      let reg = new RegExp(/(\/)/g);
      let path = oldName.substr(1);
      path = path.replace(reg, "_");
      console.log(path);

      this.excludeName.push(path);
      this.$nextTick(() => {
        let index = this.excludeName.indexOf(path);
        this.excludeName.splice(index, 1);
      });
    }
  },
  components: {
    Tabs,
    Pagination,
    HomeHead
  }
};
</script>

