}
*/
.el-header {
  padding-left: 0;
  background-color: $themeColor;
  display: flex;
  justify-content: center;
  align-items: center;

  .logo {
    height: 100%;
  }
  .userIcon {
    width: 40px;
    height: 40px;
  }
  span {
    padding-left: 20px;
    flex: 1;
    color: #fff;
    font-size: 24px;
  }
}

// asider
.el-aside {
  background-color: #333744;
  .el-menu {
    border-right: none;
  }
}

@keyframes bellRotate {
  0%,
  20% {
    transform: rotateZ(0);
  }

  30%,
  50%,
  55% {
    transform: rotateZ(-6deg);
  }
  35%,
  40%,
  60% {
    transform: rotateZ(6deg);
  }
  70%,
  100% {
    transform: rotateZ(0);
  }
}

// 信息弹窗的样式
.msg_pagination {
  text-align: center;
}
.msgbody {
  position: relative;
  .msg_wrap {
    height: 40vh;
    overflow: auto;
  }
}
.msg_wrap {
  .title {
    font-size: 20px;
    color: #f55;
  }
  .content {
    text-indent: 24px;
  }
}
.issue_time {
  position: absolute;
  bottom: -20px;
  right: 0;
}
</style>
