<template>
  <el-pagination
    ref="paginationRef"
    :background="background"
    :layout="layout"
    :total="total"
    :page-sizes="pageSizes"
    :page-size.sync="queryInfo.pageParam.pageSize"
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
  ></el-pagination>
</template>

<script>
export default {
  props: {
    background: {
      type: Boolean,
      default: true
    },
    layout: {
      type: String,
      default: "total,sizes,prev, pager, next,jumper"
    },
    total: {
      type: Number,
      default: 0
    },
    pageSizes: {
      type: Array,
      default() {
        return [];
      }
    },
    queryInfo: {
      type: Object,
      default() {
        return {
          pageParam: {
            pageSize: 1,
            page: 1
          }
        };
      }
    },
    queryFunc: {
      type: Function,
      default() {
        return function() {};
      }
    }
  },

  methods: {
    // 改变 pagesize
    handleSizeChange(newsize) {
      this.queryInfo.pageParam.pageSize = newsize;
      this.queryFunc();
    },
    // 改变 page
    handleCurrentChange(newnum) {
      this.queryInfo.pageParam.page = newnum;
      this.queryFunc();
    }
  }
};
</script>

<style lang="scss" scoped>
.el-pagination {
  margin-top: 20px;
}
</style>
