<template>
  <div class="upgrade">
    <div class="introduce" @click="handleChooseBoard">
      <board
        :data-typenum="item.typenum"
        :boardObj="{...item}"
        v-for="item in boardList"
        :key="item.typenum"
      ></board>
    </div>

    <el-dialog title="升级会员" :visible.sync="upgradeDialog" width="40%" @close="handleCloseDialog">
      <div class="dia_body">
        <p>用户名称：{{username}}</p>
        <p v-if="this.typenum!=null">会员类型：{{commonFn.MemberType(this.typenum).name}}</p>
        <el-input v-if="showCode" v-model="userParams.agency_code" placeholder="请填写邀请码"></el-input>
      </div>
      <template #footer class="dialog-footer">
        <el-button @click="upgradeDialog = false">取 消</el-button>
        <el-button type="primary" @click="handleSureUpgrade">确 定</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import Board from "@/components/Board.vue";
import { mapState, mapMutations } from "vuex";
import * as type from "@/store/mutation-type.js";

import { registerMemberApi, getSysUserInfoApi } from "@/api/api.js";

export default {
  data() {
    return {
      upgradeDialog: false,
      showCode: false,
      typenum: null,
      username: "",
      userParams: {
        user_id: null,
        agency_code: ""
      },
      boardList: [
        {
          typenum: 1,
          price: 499,
          memberName: "普通会员",
          boardBackColor: "#ccc",
          pseudoBackColor: "#aaa"
        },
        {
          typenum: 2,
          price: 879,
          memberName: "青铜会员",
          boardBackColor: "#d8ac4c",
          pseudoBackColor: "#edc880"
        },
        {
          typenum: 3,
          price: 999,
          memberName: "白金会员",
          boardBackColor: "#eee",
          pseudoBackColor: "#ccc"
        },
        {
          typenum: 4,
          price: 3999,
          memberName: "钻石会员",
          boardBackColor: "#AADCE8",
          pseudoBackColor: "#AAccc8"
        }
      ]
    };
  },
  computed: {
    ...mapState(["ownInfo"])
  },
  components: {
    Board
  },
  methods: {
    ...mapMutations([type.CHANGE_OWNINFO]),
    async handleChooseBoard(ev) {
      console.dir(ev.target.dataset.typenum);

      let params = {
        id: this.ownInfo.user_id,
        login_account: this.ownInfo.login_account
      };
      let res = await getSysUserInfoApi(params);
      if (res.code != 10000) {
        this.$message.error(res.message);
      }

      if (res.value.agency_id == 0) {
        this.showCode = true;
      } else {
        this.showCode = false;
      }

      // 根据 typenum 弹窗
      this.username = this.ownInfo.login_account;
      this.typenum = Number(ev.target.dataset.typenum);
      this.upgradeDialog = true;
    },

    handleCloseDialog() {
      Object.assign(this.userParams, this.$options.data().userParams);
      console.log("关闭窗口");
    },
    // 升级会员
    async handleSureUpgrade() {
      console.log(this.ownInfo);
      if (this.ownInfo.user_type != 2) {
        return this.$message.info("你不需要是会员~");
      }

      if (this.ownInfo.member == 1 || this.ownInfo.agency == 1) {
        return this.$message.info("你已经是会员了～");
      }
      this.userParams.user_id = this.ownInfo.user_id;

      if (!this.userParams.agency_code.trim()) {
        this.userParams.agency_code = null;
      }
      console.log(this.userParams);
      let res = await registerMemberApi(this.userParams);

      if (res.code != 10000) {
        return this.$message.error(res.message);
      }
      console.log(res);

      this[type.CHANGE_OWNINFO]({ key: "member", value: 1 });

      let obj = {
        ...this.ownInfo
      };

      window.sessionStorage.setItem("ownInfo", JSON.stringify(obj));

      this.$message.success("升级成功~");
      this.upgradeDialog = false;
      console.log("确定升级");
      // location.reload();
    }
  }
};
</script>

<style lang="scss" scoped>
.introduce {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>